body {
    height: 100vh;
    font-family: 'PlayPen', var(--fontstack);
}

#content{
    height: 100%;
    display: grid;
    grid-template-columns: 225px 1fr;
    grid-template-rows: 125px 1fr;
}

/* Header Styling yay!  */

.header {
    background-color: var(--sandBlue);
    grid-column: 1/3;
    grid-row: 1;
}

.header-text {
    font-size: 3.5rem;
    font-weight: bold;
    font-style: italic;
    color: white;
}

/* Sidebar Styling yay! */

.sidebar {
    background-color: var(--lightBlue);
    display: grid;
    grid-template-rows: 50px 1fr 95px;
    grid-row: 2;
    grid-column: 1;
}

.sidebar-title {
    font-size: 2rem;
    padding-left: 10px;
}

.sidebar-list {
    width: 80%;
    margin: 10px auto;
}

.list-item-container {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.sidebar-list-item {
    font-size: 1.2rem;
    list-style: none;
}

.sidebar-list-item:hover {
    background-color: var(--darkerLightBlue);
    color: var(--neutralWhite);
}

.sidebar-list-bttn-container {
    height: 100%;
    display: flex;
    align-items: center;
    flex: 0 0 auto;
}

.sidebar-list-delete {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.sidebar-list-edit {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.sidebar-list-bttn {
    height: 25px;
    width: 25px;
    margin-left: 3px;
    border-radius: 5px;
    border: 1px solid black;
}

.sidebar-options {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.sidebar-bttn {
    width: 200px;
    padding: 10px;
    margin-top: 5px;
    border: 1px solid gray;
    border-radius: 5px;
}

/* Project View Styling yay! */

.project-view-container {
    background-color: var(--lightGray);
    display: grid;
    grid-template-rows: 75px 1fr 75px;
    grid-template-columns: 1fr;
    grid-column: 2;
    grid-row: 2;
}

.project-view-title {
    font-size: 1.5rem;
    margin-bottom: 25px;
    text-align: center;
    grid-row: 1;
    align-self: center;
}

.project-view-list-container {
    grid-row: 2;
}

.todo-list-item {
    width: max(500px, 70%);
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.todo-info-container {
    padding: 10px;
    border: 1px solid black;
    border-radius: 15px;
    box-shadow: 0px 5px 5px rgba(0, 0, 0, 0.4);
    flex: 1 1 auto;
}

.low-priority {
    border: 2px solid yellowgreen
}

.high-priority {
    border: 2px solid pink
}

.todo-info-container:hover {
    background-color: white;
}

.todo-item-due-date {
    font-style: italic;
    text-align: right;
}

.todo-item-bttn-container {
    margin-left: 5px;
}

.todo-item-delete {
    height: 35px;
    width: 35px;
    border-radius: 5px;
    border: 1px solid black;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.project-view-options {
    display: flex;
    justify-content: center;
    grid-row: 3;
}

.project-view-bttn {
    width: max(300px, 60%);
    padding: 10px;
    border: 1px solid gray;
    border-radius: 5px;
    align-self: center;
}

/* -----------Todo View Styling Yay!------------- */

.todo-dialog{
    width: 350px;
    height: 600px;
}

